@use '../var' as *;
// overflow: hidden; //для скрытия не вмещающегося контента

.input {
  background-color: transparent;
  border: 1px solid $border-color;
  border-radius: 21px;
  line-height: calc(28 / $p);
  align-items: left;
  color: $input-color;
  transition: all 0.3s ease-in-out;
  &::placeholder {
    color: $input-color;
    // background-color: transparent;
    &:hover {
      color: #000;
    }
  }
  &:hover {
    color: $title-color;
  }
  &:focus {
    border-color: $title-color;
    color: $title-color;
  }
}

.btn {
  background-color: $bg-btn;
  color: $bg-color;
  font-size: $p;
  border-radius: 21px;
  font-weight: 600;
  line-height: calc(28 / $p);
  align-items: center;
  border: 1px $bg-btn solid;
  transition: background-color, color, 0.3s ease-out;
  user-select: none; // запрет выделение текста внутри элемента

  &:hover {
    background-color: transparent;
    color: $bg-btn;
  }
  &:active {
    transform: scale(0.96);
  }
}
